import React, { useState } from "react";
import "../node_modules/font-awesome/css/font-awesome.min.css";
import Header from "./components/header";
import AddNewTask from "./components/addNewTask";
import SimpleContext from "./context/SimpleContext";
import Preview2 from "./components/preview2";
import Completed2 from "./components/completed2";

import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Mobile from "./mobile";
import PC from "./pc";

const App = () => {
  const [getTasksList, setTasksList] = useState([]);
  const [getTask, setTask] = useState("");
  const [getcompleted, setcompleted] = useState([]);
  const [getAddButton, setAddButton] = useState(" ");
  const [getAddButtonColor, setAddButtonColor] = useState("btn btn-primary");
  const [getNumber, setNumber] = useState(1);
  const [getTime, setTime] = useState("");

  const handleSetTask = (event) => {

    setTask(event.target.value);
  };

  const handleSetTime = (event) => {
    setTime(event.target.value);
    console.log(getTime);
  };

  const handleAddTask = () => {
    setAddButton("  ");
    setAddButtonColor("btn btn-primary");
   
    if(getTask!=="" && getTask!==" "){
    const getTasksList1 = [...getTasksList];
      const taskObject = {
        id: getNumber,
        taskTitle: getTask,
        taskTime: getTime,
      };
      getTasksList1.push(taskObject);
  
      getTasksList1.sort((a, b) => {
        return a.id - b.id;
      });
  
      setTasksList(getTasksList1);
      setTask("");
      setNumber(getNumber + 1);
      document.getElementById("inputee").classList.remove("input-error")
    
      toast(` ${getTask}   !`, {
        position: "bottom-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });}
      else{
       document.getElementById("inputee").classList.add("input-error")
        alert("    .")
      }
    };
    

  const handleDo = (id) => {
    const z = getTasksList[id];
    const getcompleted1 = [...getcompleted];

    const completedObject = {
      id: z.id,
      taskCompleted: z.taskTitle,
      taskTime: z.taskTime,
    };

    getcompleted1.push(completedObject);
    setcompleted(getcompleted1);

    const y = getTasksList.filter((p, index) => index !== id);
    setTasksList(y);

    toast.success(` ${z.taskTitle}   !`, {
      position: "bottom-right",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    });
  };

  const handleDeleteTask = (id) => {
    const y = getTasksList.filter((p, index) => index !== id);
    setTasksList(y);

    const x = getTasksList[id];

    toast.error(` ${x.taskTitle}   !`, {
      position: "bottom-right",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    });
  };

  const handleReturn = (id) => {
    const y = getcompleted.filter((p, index) => index !== id);
    setcompleted(y);
    const z = getcompleted[id].id;
    const x = getcompleted[id].taskCompleted;
    const w = getcompleted[id].taskTime;

    const getTasksList1 = [...getTasksList];
    const taskObject = {
      id: z,
      taskTitle: x,
      taskTime: w,
    };
    getTasksList1.push(taskObject);

    getTasksList1.sort((a, b) => {
      return a.id - b.id;
    });

    setTasksList(getTasksList1);
  };

  const handleEdit = (id) => {
    const y = getTasksList.filter((p, index) => index !== id);
    setTasksList(y);
    const z = getTasksList[id];
    setTask(z.taskTitle);
    setNumber(id + 1);
    setAddButton("");
    setAddButtonColor("btn btn-warning");
    document.getElementById("inputee").focus();
    console.log(id);
  };
  
  // let previewTask=[]
  //  getTasksList.map((item,index)=>{
  //    previewTask[index]=[(<Preview matn={item}/>)]
  //  })

  let X

  if(window.innerWidth>600){
    X=(<PC/>)
  }else{
    X=(<Mobile/>)
  }
  
  
  return (
    <SimpleContext.Provider
      value={{
        taskList: getTasksList,
        task: getTask,
        getcompleted: getcompleted,
        addButton: getAddButton,
        addButtonColor: getAddButtonColor,
        number: getNumber,
        time: getTime,
        handleSetTask: handleSetTask,
        handleAddTask: handleAddTask,
        handleDo: handleDo,
        handleDeleteTask: handleDeleteTask,
        handleReturn: handleReturn,
        handleEdit: handleEdit,
        handleSetTime: handleSetTime,
      }}
    >
      <div dir="rtl" lang="fa">
        <ToastContainer />
        <Header />
        <AddNewTask />
        {X}
       </div>
    </SimpleContext.Provider>
  );
};

export default App;
