import React, { useContext } from "react";
import Completed2 from "./components/completed2";
import Preview2 from "./components/preview2";
import SimpleContext from "./context/SimpleContext";

const PC = () => {
    const context=useContext(SimpleContext)
  return (
      
    <div className="container">
      <div className="row justify-content-between">
        <div className="col-sm-7 mt-5">
          <h4 className="text-center text-secondary">
             {" "}
            <span className="badge rounded-pill bg-primary text-white ml-2">
              {context.taskList.length}
            </span>
          </h4>
          <Preview2 />
        </div>
        <div className="col-sm-4 mt-5 ">
          <h4 className="text-center text-secondary">
               
            <span className="badge rounded-pill bg-primary text-white">
              {context.getcompleted.length}
            </span>
          </h4>
          <Completed2 />
        </div>
      </div>
    </div>

);
};

export default PC;
